.search__root {
	padding: 0.625rem 1rem;
	display: flex;
	width: 100%;
	max-width: 350px;
	min-width: 250px;
	height: fit-content;
	z-index: 1000;
	flex: 1;
	margin: 0 auto;
}

.search__autocomplete {
	width: inherit;
}

@media (max-width: 600px) {
	.search__root {
		max-width: 100%;
	}
}
